.App{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  background: #43AA8B;
}

#quote-box{
  height: 315px;
  width: 600px;
  background: #577590;
  border-radius: 55px 55px 55px 55px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 55px;
  position: absolute;
}

#text{

  text-align: center;
}

#author{
  position: absolute;
  right: 30px;
  bottom: 25px;
}

#new-quote{
  position: absolute;
  bottom: 20px;
  left: 20px;
  text-decoration: none;
  border-radius: 12px;
  border: none;
  padding: 10px 24px;
  background: #F8961E;
}

#tweet-quote{
  position: absolute;
  bottom: 20px;
  text-decoration: none;
  border-radius: 50%;
  padding: 4px;
  border: none;
  background: #F8961E;
  margin: 0;
}

#tweet-icon{
  height: 22px;
  fill: red !important;
}

#heading{
  background: #F3722C;
  z-index: 2;
  position: absolute;
  top: -30px;
  font-size: 36px;
  padding: 5px 15px;
  border-radius: 20px 5px 20px 5px;
}